import React from 'react'
import PropTypes from 'prop-types'
import store from '../../../config/store'

import wildback from '../../../images/thewild/wildback.png'
import castleback from '../../../images/castle/castleback.png'
import paradiseback from '../../../images/paradise/paradiseback.png'
import spaceback from '../../../images/space/spaceback.png'
import landscapeback from '../../../images/landscape/landscapeback.png'
import beachback from '../../../images/beach/beachback.png'
import dragonback from '../../../images/dragons/dragonback.png'

import './styles.css'

export default function Card({
  handleClick,
  flipped,
  solved,
  id,
  height,
  type,
  width,
  disabled,
}) {

  let back

  const getBack = () => {
    switch(store.getState().home.game) {
      case 'Castle':
        return back = castleback
      case 'TheWild':
        return back = wildback
      case 'Paradise':
        return back = paradiseback
      case 'Space':
        return back = spaceback
      case 'Landscape':
        return back = landscapeback
      case 'Beach':
        return back = beachback
      case 'Dragon':
        return back = dragonback
      default: return back = wildback
    }
  }

  return (
    <div
      className={`flip-container ${flipped ? 'flipped' : ''}`}
      style={{
        width,
        height,
      }}
      onClick={() => (disabled ? null : handleClick(id))}
    >
      <div className='flipper'>
        <img
          alt='card'
          className={flipped ? 'front' : 'back'}
          src={flipped || solved ? type : getBack()}
          style={{ width, height }}
        />
      </div>
    </div>
  )
}

Card.propTypes = {
  type: PropTypes.string.isRequired,
  id: PropTypes.number.isRequired,
  flipped: PropTypes.bool.isRequired,
  solved: PropTypes.bool.isRequired,
  height: PropTypes.number.isRequired,
  width: PropTypes.number.isRequired,
  handleClick: PropTypes.func.isRequired,
  disabled: PropTypes.bool.isRequired,
}