import React, { useState, useEffect } from 'react'
import Board from './board'
import store from '../../config/store'

export default function CardGame(props) {
  const initializeDeck = props
  const [cards, setCards] = useState([])
  const [flipped, setFlipped] = useState([])
  const [solved, setSolved] = useState([])
  const [dimension, setDimension] = useState(400)
  const [disabled, setDisabled] = useState(false)

  useEffect(() => {
    resizeBoard()
    setCards(initializeDeck.initializeDeck())
  }, [])

  useEffect(() => {
    preloadImages()
  }, cards)

  useEffect(() => {
    const resizeListener = window.addEventListener('resize', resizeBoard)

    return () => window.removeEventListener('resize', resizeListener)
  })

  const preloadImages = () =>
    cards.map((card) => {
      const src = card.type
      new Image().src = src
    })

  const resizeBoard = () => {
    setDimension(
      Math.min(
        document.documentElement.clientWidth,
        document.documentElement.clientHeight,
      ),
    )
  }
  
  //const sameCardClicked = (id) => flipped.includes(id)
  const sameCardClickedTwice = (flipped, id) => {
    if (flipped[0] === id) {
      return true
    } else {
      return false
    }
  }

  function allSolved(solved) {
    //store.dispatch({
    //  type: 'SOLVE_CARDS',
    //  payload: {
    //      solved: solved
    //  }
    //})
    const game = store.getState().home.game
    //maybe this doesn't even have to be done since the cards and the board are already here?

  }

  function checkAllSolved() {
    //if(check array or whatever) {
      allSolved('True')
    //}
  }

  const isAMatch = (id) => {
    const clickedCard = cards.find((card) => card.id === id)
    const flippedCard = cards.find((card) => flipped[0] === card.id)
    return flippedCard.type === clickedCard.type
  }

  const resetCards = () => {
    setFlipped([])
    setDisabled(false)
  }

  const handleClick = (id) => {
    
    setDisabled(true)
    if (flipped.length === 0) {
      setFlipped((flipped) => [...flipped, id])
      setDisabled(false)
    } else {
      if (sameCardClickedTwice(flipped, id)){
        setTimeout(resetCards, 500)
        return
      }
      setFlipped((flipped) => [...flipped, id])
      if (isAMatch(id)) {
        setSolved([...solved, ...flipped, id])
        resetCards()
      } else {
        setTimeout(resetCards, 1000)
      }
    }

  }

  function goHome() {
    store.dispatch({
      type: 'PLAY_GAME',
      payload: {
          game: 'Home'
      }
    })
  }

  return (
    <div style={{
        position: 'relative',
        width: '500px',
        height: '560px',
        margin: '20px auto',
      }}>
      <h2 style={{cursor: 'pointer'}} onClick={() => goHome()}>Home / Memory Games</h2>

      <Board
        cards={cards}
        flipped={flipped}
        solved={solved}
        dimension={dimension}
        handleClick={handleClick}
        disabled={disabled}
      />
    </div>
  )
}