import React, { Component } from 'react'
import { connect } from 'react-redux'
import Games from '../src/components/games-home'
import TheWild from '../src/components/TheWild'
import Castle from '../src/components/Castle'
import Paradise from '../src/components/Paradise'
import Space from '../src/components/Space'
import Landscape from '../src/components/Landscape'
import Beach from '../src/components/Beach'
import Dragon from '../src/components/Dragon'

class App extends Component {
  render() {
    let app

    function setApp(display) {
      switch(display) {
        case 'Home': 
          return app = <Games />
        case 'Castle':
          return app = <Castle />
        case 'Paradise':
          return app = <Paradise />
        case 'Space':
          return app = <Space />
        case 'TheWild':
          return app = <TheWild />
        case 'Landscape':
          return app = <Landscape />
        case 'Beach':
          return app = <Beach />
        case 'Dragon':
          return app = <Dragon />
        default: return app = <Games />
      }
    }

    return (
      <div className="home">
        <div className='home-element'>
          {setApp(this.props.home.game)}
        </div>
      </div>
    )
  }
}

function mapStateToProps(state) {
  return {
    home: state.home
  }
}

export default connect(mapStateToProps)(App)
