const initialState = {
  game: 'Home',
  solved: 'False'
}

const formReducer = (state=initialState, action) => {
  switch(action.type) {
    case 'PLAY_GAME':
      return {
        game: action.payload.game
      }
    case 'SOLVE_CARDS':
      return {
        solved: action.payload.solved
      }
    default: return state
  }
}

export default formReducer