import React, { Component } from 'react'
import store from '../../config/store'
import './styles.css'
import { forest, dragons, paradise, castle, volcano, football, zoo, purplespaceship, yacht} from '../../images/home'

class Games extends Component {

  render() {

    function renderGameWorld(game) {
      store.dispatch({
        type: 'PLAY_GAME',
        payload: {
            game: game
        }
      })
    }

    return (
      <div className='color-container'>
        <div className='color-container'>
          <div style={{display: 'table-cell', verticalAlign: 'middle', width: '140px'}}><p>&nbsp;</p></div>
          <div className='colorpack colorpack-1'>
            <img onClick={() => renderGameWorld('Paradise')} className='color-image' src={paradise}></img>
          </div>
          <div style={{display: 'table-cell', verticalAlign: 'middle', width: '40px'}}><p>&nbsp;</p></div>
          <div className='colorpack colorpack-2'>
            <img onClick={() => renderGameWorld('Dragon')} className='color-image' src={dragons}></img>
          </div>
          <div style={{display: 'table-cell', verticalAlign: 'middle', width: '40px'}}><p>&nbsp;</p></div>
          <div className='colorpack colorpack-4'>
            <img onClick={() => renderGameWorld('Landscape')} className='color-image' src={forest}></img>
          </div>
          <div style={{display: 'table-cell', verticalAlign: 'middle', width: '40px'}}><p>&nbsp;</p></div>
          <div className='colorpack colorpack-3'>
            <img onClick={() => renderGameWorld('Castle')} className='color-image' src={castle}></img>
          </div>
          <div style={{display: 'table-cell', verticalAlign: 'middle', width: '40px'}}><p>&nbsp;</p></div>
          <div className='colorpack colorpack-1'>
            <img onClick={() => renderGameWorld('Space')} className='color-image' src={purplespaceship}></img>
          </div>
        </div>

        <div className='color-container' style={{float: 'center'}}>
          <div style={{display: 'table-cell', verticalAlign: 'middle', width: '325px'}}><p>&nbsp;</p></div>
          <div className='colorpack colorpack-5'>
            <img onClick={() => renderGameWorld('Beach')} className='color-image' src={yacht}></img>
          </div>
          <div style={{display: 'table-cell', verticalAlign: 'middle', width: '40px'}}><p>&nbsp;</p></div>
          <div className='colorpack colorpack-4'>
            <img onClick={() => renderGameWorld('TheWild')} className='color-image' src={zoo}></img>
          </div>
        </div>
      </div>
    )
  }
}

export default Games
