import {castle, crown, dance, flowers, horse, knight, queen, sparkles} from '../../images/castle'

function shuffle(array) {
  const _array = array.slice(0)
  for (var i = _array.length - 1; i > 0; i--) {
    var j = Math.floor(Math.random() * (i + 1))
    var temp = _array[i]
    _array[i] = _array[j]
    _array[j] = temp
  }

  return _array
}

export default function initializeDeck() {
  let id = 0
  const cards = [castle, crown, dance, flowers, horse, knight, queen, sparkles].reduce((acc, type) => {
    acc.push(...[{
      id: id++,
      type
    }, {
      id: id++,
      type
    }])
    return acc
  }, [])

  //if(this.props.home.solved==='True') {
  //  return cards
  //} else {
    return shuffle(cards)
  //}
}

